'use client'
import React from "react";
import { useRouter } from "next/navigation";
import { Download, Pin, Trash2 } from "lucide-react";
import axios from "axios";
import { useState, useEffect } from "react";
import CreateNewProject from "../dashboard/Create_Project_modal";
import useSWR from "swr";
import { toast } from "react-toastify";
import Image from "next/image";
// import NoticeBoard from "../overview/NoticeBoard";
import NoticeBoard from '../../../public/assets/Notice Board Illustration.png'
const MyProjects = () => {

  // const [projectData, setProjectData] = useState<any[]>([]);
  const [token, setToken]= useState<string | null>(null);
  const [modal, setModal]= useState<boolean>(false);
  const [isToast, setToast]= useState(false);
  if(isToast){
    toast.success("Project Created Successfully");
    setToast(false);
  }
  const router = useRouter();
  useEffect(()=>{
    const storedtoken = localStorage.getItem("token"); 
    setToken(storedtoken);

  },[])
  
  const fetcher = async ([url, token]: [string, string | null]) => {
    if (!token) return [];
    try {
      const response = await axios.get(url, { headers: { Authorization: `Bearer ${token}` } });
      return response.data;
    } catch (error) {
      console.error("Error fetching projects:", error);
      throw error;
    }
  };
  const { data: projects, error, mutate } = useSWR(
    token ? [`${process.env.NEXT_PUBLIC_API_URL}/projects/my-projects`, token] : null,
    fetcher,
    { revalidateOnFocus: false,
      dedupingInterval: 2000 
     } 
  );
  const handleProjectCreated = async () => {
    // toast.success("Project successfully created")
    await mutate(); 
    // toast.success("Project successfully created")
    setToast(true)
  };
   
  const colors = [
    "bg-teal-200 text-teal-600",
    "bg-rose-200 text-rose-600",
    "bg-orange-200 text-orange-600",
    "bg-blue-200 text-blue-600",
    "bg-blue-200 text-blue-600",
    "bg-orange-200 text-orange-600",
  ];
  

  
  // console.log("Token at MyProjects.tsx:73", token);

  if (error) {
    console.error("Fetching error:", error.response ? error.response.data : error.message);
    return <p className="text-red-500">Error fetching projects</p>;
  }
  if (!projects) return <p className="text-gray-400">Loading projects...</p>;
  const handleClick = (project: any) => {
    // console.log(project._id)
    router.push(`/main/${project._id}/overview`); 
  };
  
  const createFirst=()=>{
    setModal(true)
  }
  const modalFalse= ()=>{
    setModal(false)
  }
  return (
    <div className="w-full mx-auto space-y-2">
      {projects.length <= 0 ? (
        <div className="text-center py-6">
          <div className="w-24 h-24 mx-auto mb-4 bg-indigo-50 rounded-full flex items-center justify-center">
            <div className="w-12 h-12 bg-indigo-100 rounded-full flex items-center justify-center">
            <Image src={NoticeBoard} alt="No projects" width={28} height={28} />
            </div>
          </div>
          <p className="text-sm text-[#4D4D4D] mb-4">No Project Found</p>
          <button className="w-full py-2 px-4 bg-[#5D56BD] text-white text-sm rounded-lg hover:bg-[#5D56BD] transition-colors" onClick={createFirst}>
            Create your first project
          </button>
        </div>
      ) : (
        <div className="space-y-2">
          
          {projects.map((project:any, index:any) => (
            <div
              key={project._id}
              className="flex items-start bg-white justify-between p-4 hover:bg-blue-50 rounded-lg max-w-10xl"
              onClick={() => handleClick(project)}
            >
              <div className="flex items-center hover:cursor-pointer">
                <div
                  className={`w-10 h-10 flex-shrink-0 ${
                    colors[Math.floor((Math.random()*10))% colors.length]
                  } rounded-full flex items-center justify-center`}
                >
                  {project.name.charAt(0).toUpperCase()}
                </div>
                <div className="pl-6">
                  <h3 className="text-sm font-medium text-[#333333]">
                    {project.name}
                  </h3>
                  <p className="text-sm text-gray-500">{project.category}</p>
                </div>
              </div>
              <div className="flex items-center space-x-2">
              </div>
            </div>
          ))}
        </div>
      )} 
      {
        modal && <CreateNewProject onClose={modalFalse} onProjectCreated={handleProjectCreated}/>
      }
    </div>
  );
};

export default MyProjects;


